	    $scope.calculateTotalAmount = function() { 
			//alert('1');
			//console.log($scope.invoice.ChargeAmount);
			$scope.amountToPaid = 0;
			$scope.isValidate  = false;
			angular.forEach($scope.invoice.ChargeAmount, function(value, key) {
			if(value!=''){		
				$scope.amountToPaid = parseFloat($scope.amountToPaid) + parseFloat(value);
			}
			if(value==null){
				 $scope.isValidate  = true;
			}
					
					
			});
			if($scope.amountToPaid > $scope.TotalAmount){
			 $scope.isValidate  = true;
			}
		};
		
		$scope.changePaymentMode = function(value) {
		if(value==true)
		{
			$scope.showCCDiv  = false;
			$scope.showACHDiv  = true;	
		}
		else
		{
			$scope.showCCDiv  = true;
			$scope.showACHDiv  = false;	
		}
	};
	 
