(function ()
{
    'use strict';

    angular
        .module('app.invoice', [])
        .config(config);

    /** @ngInject */
    function config($stateProvider, $translatePartialLoaderProvider, msApiProvider, msNavigationServiceProvider)
    {
        // State
        $stateProvider
            .state('app.invoice', {
                url    : '/invoice/:token',
                views  : {
                    'content@app': {
                        templateUrl: 'app/main/invoice_view/invoice.html',
                        controller : 'InvoiceController as vm'
                    }
                },
                resolve: {
                    Invoice: function (msApi)
                    {
                        return msApi.resolve('invoice@get');
                    }
                }
            })
		.state('app.success', {
                url    : '/success/:id',
                views  : {
                    'content@app': {
                        templateUrl: 'app/main/invoice_view/thankyou.html',
                        controller : 'SuccessController as vm'
                    }
                },
                resolve: {
                    Invoice: function (msApi)
                    {
                        return msApi.resolve('invoice@get');
                    }
                }
            });	

        // Translation
        $translatePartialLoaderProvider.addPart('app/main/invoice_view');

        // Api
        msApiProvider.register('invoice', ['app/data/invoice/invoice.json']);

        // Navigation
       /*msNavigationServiceProvider.saveItem('fuse', {
            title : 'Invoice',
            group : true,
            weight: 1
        });

        msNavigationServiceProvider.saveItem('fuse.sample', {
            title    : 'Invoice',
            icon     : 'icon-tile-four',
            state    : 'app.invoice',
            stateParams: {
                'param1': 'page'
             },
            translate: 'SAMPLE.SAMPLE_NAV',
            weight   : 1
        });*/
    }
})();
