      console.log($stateParams.id)
      if ($stateParams.id && $stateParams.token) {
        vm.invoice_id = $stateParams.id;
        vm.authorization = $stateParams.token;
        vm.appId = $stateParams.app_id;
      } else {
        vm.invoice_id = 0;
        vm.authorization = $stateParams.token;
        vm.appId = $stateParams.app_id;
      }
