(function () {
  'use strict';
  angular
    .module('fuse')
    .factory('InvoiceModel', InvoiceModel);

  InvoiceModel.$inject = ['$http', 'Clique', '$httpParamSerializer'];

  function InvoiceModel($http, Clique, $httpParamSerializer) {
    var service = {};

    service.GetInvoiceDetails = GetInvoiceDetails;
    service.GetInvoiceTemplate = GetInvoiceTemplate;
    service.CardConnectPayment = CardConnectPayment;
    service.customerFeedback = customerFeedback;
    service.GetAllInvoice = GetAllInvoice;
    service.ProfileGet = ProfileGet;
    service.ProfileDelete = ProfileDelete;
    service.ProfileEdit = ProfileEdit;
    service.CustomerTransactionGet = CustomerTransactionGet;
    service.Signup = Signup;
    service.Login = Login;
    service.ForgetPassword = ForgetPassword;
    service.ProfileAdd = ProfileAdd;
    service.GetPaymentMethods = GetPaymentMethods;
    service.AllTransaction = AllTransaction;
    service.CardConnectPaymentPartially = CardConnectPaymentPartially;
    service.downloadReceipt = downloadReceipt;
    service.GetSettings = GetSettings;
    service.searchTransaction = searchTransaction;
    service.GetInvoiceTemplateBase64 = GetInvoiceTemplateBase64;
    service.GetInvoiceDetailsBase64 = GetInvoiceDetailsBase64;

    return service;

    function GetInvoiceDetailsBase64(token) {
      //     return Clique.callService('get','/erp/quickbooks/getinvoice/','').then(handleSuccess, handleError);
      return Clique.callService('get', '/erp/quickbooks/getinvoice/?q=' + token, '').then(handleSuccess, handleError);
    }

    function GetInvoiceTemplateBase64(Invoice_id, temp_id, inv_color) {
      //return Clique.callService('get','/erp/quickbooks/invoice/preview/','').then(handleSuccess, handleError);
      return Clique.callService('get', '/erp/quickbooks/invoice/preview/?invoice_id=' + Invoice_id + '&temp_id=' + temp_id + '&temp_color=' + inv_color, '').then(handleSuccess, handleError);
    }

    function GetSettings() {
      return Clique.callService('get', '/settings/', '').then(handleSuccess, handleError);
    }

    function GetInvoiceDetails(token) {
      //     return Clique.callService('get','/erp/quickbooks/getinvoice/','').then(handleSuccess, handleError);
      return Clique.callService('get', '/erp/quickbooks/getinvoice/?q=' + token, '').then(handleSuccess, handleError);
    }
    // function GetInvoiceDetails() {
    //   return Clique.callService('get', '/erp/quickbooks/getinvoice/', '').then(handleSuccess, handleError);
    //   //return Clique.callService('get','/erp/quickbooks/getinvoice/?q='+ token,'').then(handleSuccess, handleError);
    // }

    function CardConnectPayment(params) {
      return Clique.callService('post', '/cardconnect/sale/', params).then(handleSuccess, handleError);
    }

    function CardConnectPaymentPartially(params) {
      return Clique.callService('post', '/cardconnect/partialsale/', params).then(handleSuccess, handleError);
    }

    function customerFeedback(params) {
      return Clique.callService('post', '/customer/feedback/', params).then(handleSuccess, handleError);
    }


    function GetInvoiceTemplate(Invoice_id) {
      //return Clique.callService('get','/erp/quickbooks/invoice/preview/','').then(handleSuccess, handleError);
      return Clique.callService('get', '/erp/quickbooks/invoice/preview/?invoice_id=' + Invoice_id, '').then(handleSuccess, handleError);
    }

    function GetAllInvoice(query) {
      var qs = $httpParamSerializer(query);
      return Clique.callService('get', '/erp/quickbooks/invoice/?' + qs, '').then(handleSuccess, handleError);
    }

    function CustomerTransactionGet(params) {
      return Clique.callService('post', '/customer/transaction/', params).then(handleSuccess, handleError);
    }

    function ProfileGet(params) {
      return Clique.callService('post', '/profile/get/', params).then(handleSuccess, handleError);
    }

    function ProfileDelete(params) {
      return Clique.callService('post', '/profile/delete/', params).then(handleSuccess, handleError);
    }

    function ProfileEdit(params) {
      return Clique.callService('post', '/profile/edit/', params).then(handleSuccess, handleError);
    }

    function ProfileAdd(params) {
      return Clique.callService('post', '/profile/add/', params).then(handleSuccess, handleError);
    }

    function Login(params) {
      return Clique.callService('post', '/account/login', params).then(handleSuccess, handleError);
    }

    function Signup(params) {
      return Clique.callService('post', '/account/signup', params).then(handleSuccess, handleError);
    }

    function ForgetPassword(params) {
      return Clique.callService('post', '/account/forget', params).then(handleSuccess, handleError);
    }

    function GetPaymentMethods() {
      return Clique.callService('get', '/paymentmethods', '').then(handleSuccess, handleError);
      //return Clique.callService('get','/erp/quickbooks/getinvoice/?q='+ token,'').then(handleSuccess, handleError);
    }

    function AllTransaction(params) {
      return Clique.callService('get', '/invoice/transactions/', params).then(handleSuccess, handleError);
    }

    function searchTransaction(params) {
      var qs = $httpParamSerializer(params);
      return Clique.callService('get', '/invoice/transactions/?' + qs, '').then(handleSuccess, handleError);
    }


    function downloadReceipt(params) {
      // var qs = $httpParamSerializer(params);
      return Clique.callService('get', '/transaction/receiptdownload/?' + params, '').then(handleSuccess, handleError);

      //return Clique.callService('get','/transaction/receiptdownload/?',params).then(handleSuccess, handleError);
    }

    // private functions

    function handleSuccess(res) {
      //console.log(res);
      return res.data;
    }

    function handleError(error) {
      //console.log(error);
      return error;
    }
  }

})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .run(runBlock);

    /** @ngInject */
    function runBlock(msUtils, fuseGenerator, fuseConfig)
    {
        /**
         * Generate extra classes based on registered themes so we
         * can use same colors with non-angular-material elements
         */
        fuseGenerator.generate();

        /**
         * Disable md-ink-ripple effects on mobile
         * if 'disableMdInkRippleOnMobile' config enabled
         */
        if ( fuseConfig.getConfig('disableMdInkRippleOnMobile') && msUtils.isMobile() )
        {
            var bodyEl = angular.element('body');
            bodyEl.attr('md-no-ink', true);
        }

        /**
         * Put isMobile() to the html as a class
         */
        if ( msUtils.isMobile() )
        {
            angular.element('html').addClass('is-mobile');
        }

        /**
         * Put browser information to the html as a class
         */
        var browserInfo = msUtils.detectBrowser();
        if ( browserInfo )
        {
            var htmlClass = browserInfo.browser + ' ' + browserInfo.version + ' ' + browserInfo.os;
            angular.element('html').addClass(htmlClass);
        }
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.core')
        .config(config);

    /** @ngInject */
    function config($ariaProvider, $logProvider, msScrollConfigProvider, $translateProvider, fuseConfigProvider)
    {
        // Enable debug logging
        $logProvider.debugEnabled(true);
        
        // angular-translate configuration
        $translateProvider.useLoader('$translatePartialLoader', {
            urlTemplate: '{part}/i18n/{lang}.json'
        });
        $translateProvider.preferredLanguage('en');
        $translateProvider.useSanitizeValueStrategy('sanitize');

        /*eslint-disable */

        // ng-aria configuration
        $ariaProvider.config({
            tabindex: false
        });

        // Fuse theme configurations
        fuseConfigProvider.config({
            'disableCustomScrollbars'        : false,
            'disableCustomScrollbarsOnMobile': true,
            'disableMdInkRippleOnMobile'     : true
        });

        // msScroll configuration
        msScrollConfigProvider.config({
            wheelPropagation: true
        });

        /*eslint-enable */
    }
})();
(function ()
{
    'use strict';

    angular
        .module('fuse')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $timeout, $state)
    {
        // Activate loading indicator
        var stateChangeStartEvent = $rootScope.$on('$stateChangeStart', function ()
        {
            $rootScope.loadingProgress = true;
        });

        // De-activate loading indicator
        var stateChangeSuccessEvent = $rootScope.$on('$stateChangeSuccess', function ()
        {
            $timeout(function ()
            {
                $rootScope.loadingProgress = false;
            });
        });

        // Store state in the root scope for easy access
        $rootScope.state = $state;

        // Cleanup
        $rootScope.$on('$destroy', function ()
        {
            stateChangeStartEvent();
            stateChangeSuccessEvent();
        });
    }
})();
(function ()
{
    'use strict';

    angular
        .module('fuse')
        .config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider, $urlRouterProvider, $locationProvider)
    {
        $locationProvider.html5Mode(true);

        $urlRouterProvider.otherwise('/login');

        /**
         * Layout Style Switcher
         *
         * This code is here for demonstration purposes.
         * If you don't need to switch between the layout
         * styles like in the demo, you can set one manually by
         * typing the template urls into the `State definitions`
         * area and remove this code
         */
        // Inject $cookies
        var $cookies;

        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies)
            {
                $cookies = _$cookies;
            }
        ]);

        // Get active layout
        var layoutStyle = $cookies.get('layoutStyle') || 'contentWithToolbar';

        var layouts = {
            verticalNavigation  : {
                main      : 'app/core/layouts/vertical-navigation.html',
                toolbar   : 'app/toolbar/layouts/vertical-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation/navigation.html'
            },
            verticalNavigationFullwidthToolbar  : {
                main      : 'app/core/layouts/vertical-navigation-fullwidth-toolbar.html',
                toolbar   : 'app/toolbar/layouts/vertical-navigation-fullwidth-toolbar/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation/navigation.html'
            },
            verticalNavigationFullwidthToolbar2  : {
                main      : 'app/core/layouts/vertical-navigation-fullwidth-toolbar-2.html',
                toolbar   : 'app/toolbar/layouts/vertical-navigation-fullwidth-toolbar-2/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation-fullwidth-toolbar-2/navigation.html'
            },
            horizontalNavigation: {
                main      : 'app/core/layouts/horizontal-navigation.html',
                toolbar   : 'app/toolbar/layouts/horizontal-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/horizontal-navigation/navigation.html'
            },
            contentOnly         : {
                main      : 'app/core/layouts/content-only.html',
                toolbar   : '',
                navigation: ''
            },
            contentWithToolbar  : {
                main      : 'app/core/layouts/content-with-toolbar.html',
                toolbar   : 'app/toolbar/layouts/content-with-toolbar/toolbar.html',
                navigation: ''
            }
        };
        // END - Layout Style Switcher

        // State definitions
        $stateProvider
            .state('app', {
                abstract: true,
                views   : {
                    'main@'         : {
                        templateUrl: layouts[layoutStyle].main,
                        controller : 'MainController as vm'
                    },
                    'toolbar@app'   : {
                        templateUrl: layouts[layoutStyle].toolbar,
                        controller : 'ToolbarController as vm'
                    },
                    'navigation@app': {
                        templateUrl: layouts[layoutStyle].navigation,
                        controller : 'NavigationController as vm'
                    },
                    'quickPanel@app': {
                        templateUrl: 'app/quick-panel/quick-panel.html',
                        controller : 'QuickPanelController as vm'
                    }
                }
            });
    }

})();

